<template>
  <div>
    <van-nav-bar
      title="备案项目列表详情"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-cell-group>
      <van-cell title="备案项目编号" :value="recProjectDetails.number" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="项目名称" :value="recProjectDetails.name" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="项目所在地" :value="recProjectDetails.city" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="总投资" :value="recProjectDetails.pri" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="项目规模及主要内容" :label="recProjectDetails.text" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="建设单位" :value="recProjectDetails.unit" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="备案机关" :value="recProjectDetails.organ" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="备案申报日期" :value="recProjectDetails.dateDay" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="复核通过日期" :value="recProjectDetails.review" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="项目起止年限" :value="recProjectDetails.date_d" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="项目当前进度" :value="recProjectDetails.schedule" />
    </van-cell-group>
  </div>
</template>

<script>
export default {
  data() {
    return {
      id: "",
      recProjectDetails: []
    };
  },
  created() {
    this.id = this.$route.query.id;
    this.$axios.get(`/recProjectDetails/${this.id}`).then(res => {
      this.recProjectDetails = res.data;
      console.log(this.recProjectDetails);
    });
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    }
  }
};
</script>
